<template>
    <div :id="comp.id" :ref="comp.id" :style="comp.componentInfo.style" >
    <swiper :options="swiperOption" ref="mySwiper" style="height:100%;">
        <swiper-slide class="swiper-no-swiping" v-for="(image,index) in imageList" :key="index">
          <img :src="image" alt="" style="width:100%;height:100%;">
        </swiper-slide>
                <div class="swiper-pagination" slot="pagination"></div>

    </swiper>
    </div>
</template>

<script>
const _ = require("lodash");
import { swiper, swiperSlide } from 'vue-awesome-swiper'
export default {
    name: "carousel",
    components:{
    "swiper": swiper,
    "swiper-slide": swiperSlide
    },
    props: {
        comp: {
            type: Object
        }
    },
    data(){
      return {
        swiperOption: {
          // 
          noSwiping: true,
          autoplay:true,
          pagination: {
            el: '.swiper-pagination',
            bulletClass : `carousel-bullet-${this.comp.id}`,
            bulletActiveClass: `carousel-bullet-active-${this.comp.id}`
          },
          on:{
              paginationRender:function(){
                console.log("Pagination Rendered")
              },
              paginationUpdate:() => {
                let defaultDotNodes = document.getElementsByClassName(`carousel-bullet-${this.comp.id}`);
                [...defaultDotNodes].reduce((pre,cur) => {
                  cur.style.cssText = this.defaultDotStyleString
                },null)
                let activeDotNode = document.getElementsByClassName(`carousel-bullet-active-${this.comp.id}`)
                activeDotNode[0].style.cssText = this.activeDotStyleString
              }
            }
        }
      }
    },
    computed:{
      imageList(){
        return this.comp.componentInfo.data.imageList
      },
      defaultDotStyleString(){
        return _.reduce(this.comp.componentInfo.data.dotStyle.default, (result, value, key) => {
          return `${result}${key}:${value};`
        },"")
      },
      activeDotStyleString(){
      return _.reduce(this.comp.componentInfo.data.dotStyle.active, (result, value, key) => {
          return `${result}${key}:${value};`
        },"")
      }
    }
}
</script>
